const entities = {
  CLIENT: 'CLIENT',
  PARTNER: 'PARTNER',
  EXECUTOR: 'EXECUTOR',
}
const form = {
  phoneNumber: '',
  token: '',
  code: '',
  firstName: '',
  lastName: '',
  middleName: '',
  email: '',
  password: '',
}
export default {
  name: 'Register',
  data() {
    return {
      step: 0,
      error: false,
      entity: entities.CLIENT,
      form: { ...form },
    }
  },
  computed: {
    enableStepTwo() {
      return this.form.email !== '' && /.+@.+\..+/.test(this.form.email)
    },
    enableStepThree() {
      return this.form.code.length === 4
    },
    enableStepFour() {
      // return form.code.length === 4
      return (
        this.form.firstName !== '' &&
        this.form.lastName !== '' &&
        this.form.middleName !== '' &&
        this.form.email !== '' &&
        this.form.phoneNumber.length > 3 &&
        this.form.password !== '' &&
        this.form.password.length >= 4
      )
    },
  },
  methods: {
    reset() {
      this.form = { ...form }
      this.step = 0
    },
    openCrm() {
      window.open("https://crm.zakazy.online/","_self")
    },
    registerEmail() {
      const { email } = this.form
      this.$axios
        .post(`/${this.entity.toLowerCase()}/register/step/1`, {
          email,
        })
        .then((r) => {
          this.step = 2
          this.form.token = r.data.data.token
        })
    },
    registerSms() {
      const { phoneNumber, code, token } = this.form
      this.$axios
        .post(`/${this.entity.toLowerCase()}/register/step/2`, {
          phoneNumber,
          code,
          token,
        })
        .then((r) => {
          this.step = 3
          this.form.token = r.data.data.token
        })
    },
    registerUser() {
      const {
        firstName,
        lastName,
        middleName,
        phoneNumber,
        password,
      } = this.form
      this.$axios
        .post(
          `/${this.entity.toLowerCase()}/register/step/3`,
          {
            firstName,
            lastName,
            middleName,
            phoneNumber,
            password,
          },
          {
            headers: {
              token: this.form.token,
            },
          }
        )
        .then(() => {
          this.step = 4
        })
    },
    register(entity) {
      this.entity = entities[entity.toUpperCase()]
      this.step = 1
    },
    typeNumber(e) {
      if (e.keyCode >= 48 && e.keyCode <= 57) {
      } else {
        e.preventDefault()
      }
    },
  },
}
